<template>
<div>
  <header-area />
  <h1 class="hero" :style="{backgroundImage: 'url(' + bg_work + ')' }">
    <span class="hero__content">About</span>
  </h1>
  <article class="mt-plus" itemscope="" itemtype="http://schema.org/Person">
    <section class="c-centering">
      <h2 class="message">
        <!-- <br class="hide-sp"> -->
        I design between people and products <br class="hide-sp">to deliver experiential value to daily life.
      </h2>
      <p class="sub-title">
        <!-- <br>
         -->
        Even if you have a good product or plan, it is meaningless if users do not find it valuable.<br>
        Through service design, I identify core values and deliver experiences in people's lives.
      </p>

      <div class="development mt-plus mb-100">
        <div class="skill c-shadow">
          <img class="skill__image" src="@/assets/image/skill-2.png" alt="">
          <div class="skill__box">
            <h2 class="title">PO</h2>
            <ul class="description">
              <li></li>
              <li>PO</li>
              <li></li>
            </ul>
          </div>
        </div>

        <div class="skill c-shadow">
          <img class="skill__image" src="@/assets/image/skill-1.png" alt="">
          <div class="skill__box">
            <h2 class="title">UX</h2>
            <ul class="description">
              <li></li>
              <li><b></b>A/B</li>
              <li><b></b></li>
            </ul>
          </div>
        </div>

        <div class="skill c-shadow">
          <img class="skill__image" src="@/assets/image/skill-3.png" alt="UI">
          <div class="skill__box">
            <h2 class="title">UI</h2>
            <ul class="description">
              <li>toBtoCWeb</li>
              <li>UI</li>
              <li>BAD UIUI</li>
            </ul>
          </div>
        </div>
      </div>
    </section>

    <section class="aboutme" :style="{backgroundImage: 'url(' + bg_akinen + ')' }">
      <div class="profiles c-centering c-shadow">
        <img class="akinen" :src="akinen" alt="">
        <div class="profile">
          <h2 class="profile__name">AKINORI OZAWA</h2>
          <p class="profile__discription">PRODUCT DESIGNER</p>
          <p class="profile__discription">
            <!-- <br> -->
            <!-- <br> -->
            Born in Nagano, Japan. Worked at Kayac.inc and Buildit.inc (educational venture) before assuming my current position. I have experience as both an engineer and designer, and am currently involved in the creation of new business for Japan's largest non-life insurance group.
          </p>
        </div>
      </div>
    </section>
  </article>

  <footer-area />
</div>
</template>

<script>
import HeaderArea from '@/components/HeaderArea'
import FooterArea from '@/components/FooterArea'

export default {
  name: 'About',
  data () {
    return {
      bg_akinen: require('@/assets/image/bg_akinen.png'),
      bg_work: require('@/assets/image/bg_work.png'),
      akinen: require('@/assets/image/akinen.jpg')
    }
  },
  components: {
    HeaderArea,
    FooterArea
  }
}
</script>

<style scoped lang="stylus">
@import "../../assets/css/common.styl"

.message
  margin-bottom -20px
  font-size 24px
  font-weight 600
  text-align center
  line-height 1.8
  letter-spacing 0.1rem

// 
.skill
  width 100%
  background-color white
  border-radius 4px
  box-sizing border-box
  overflow hidden

  &__image
    width 100%

  &__box
    padding 20px

.title
  font-size 20px
  font-weight 600
  margin-bottom 12px
  text-align center
  color #22746C

.description
  > li
    list-style-type disc
    line-height 1.8
    margin 0 0 6px 20px

.development
  display flex
  flex-direction row

  @media (max-width: 960px)
    flex-direction column

  > .skill
    &:last-child
      margin 0

    &:not(:last-child)
      margin 0 20px 0 0

    @media (max-width: 960px)
      &:not(:last-child)
          margin 0 0 20px 0

// 
.aboutme
  width 100%
  padding 88px 0
  box-sizing border-box
  background 50% 50% / cover no-repeat

.profiles
  position relative
  padding 20px
  border-radius 4px
  background-color white
  box-sizing border-box

  @media (max-width: 768px)
    height auto
    padding 20px

.akinen
  position absolute
  top 20px
  left 20px
  width 200px
  height 200px
  border-radius 4px

  @media (max-width: 768px)
    position relative
    top 0
    left 0
    margin 0 auto
    display block

.profile
  margin 20px 20px 20px 240px

  @media (max-width: 768px)
    height auto
    margin 0

  &__name
    font-size 32px
    font-weight bold
    line-height 2rem
    letter-spacing 0.2rem

    @media (max-width: 768px)
      text-align center

  &__discription
    padding-top 8px
    line-height 2.0
    letter-spacing 0.04rem

    @media (max-width: 768px)
      text-align center
</style>



